/**
 * ÐÐ°Ð·Ð¾Ð²Ð°Ñ ÐºÐ¾Ð»Ð»ÐµÐºÑÐ¸Ñ Ð´Ð»Ñ Ð²ÑÐµÑ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½ÑÐ¾Ð²
*/
export abstract class BaseItemCollection<T extends BaseItem<BaseParams>>
{
    // #region Ð¡Ð²Ð¾Ð¹ÑÑÐ²Ð°

    // 
    /**
     * Ð£Ð½Ð¸ÐºÐ°Ð»ÑÐ½Ð¾Ðµ Ð¸Ð¼Ñ ÐºÐ¾Ð»Ð»ÐµÐºÑÐ¸Ð¸. ÐÐ° ÑÐ½Ð¸ÐºÐ°Ð»ÑÐ½Ð¾ÑÑÑÑ ÑÐ»ÐµÐ´Ð¸Ð¼ ÑÐ½Ð°ÑÑÐ¶Ð¸!
     * ÐÐ¾Ð·Ð¼Ð¾Ð¶Ð½Ð¾ Ð¿ÑÐ¸Ð³Ð¾Ð´Ð¸ÑÑÑ Ð´Ð»Ñ Ð¿Ð¾Ð¸ÑÐºÐ° Ð½ÑÐ¶Ð½Ð¾Ð¹ ÐºÐ¾Ð»Ð»ÐµÐºÑÐ¸Ð¸ Ð½Ð° ÑÑÐ¾Ð²Ð½Ðµ Ð¿ÑÐ¸Ð»Ð¾Ð¶ÐµÐ½Ð¸Ñ. ÐÐ¾ÐºÐ° Ð½Ð¸Ð³Ð´Ðµ Ð½Ðµ Ð¸ÑÐ¿Ð¾Ð»ÑÐ·ÑÐµÑÑÑ.
     * ÐÐ¾ÐºÐ° Ð´Ð»Ñ Ð¿Ð¾Ð¸ÑÐºÐ° Ð¸ÑÐ¿Ð¾Ð»ÑÐ·ÑÐµÐ¼ Params.Selector
     */
    //Name: string;

    /**
     * ÐÐ¾Ð¿Ð¸Ñ Ð²ÑÐ¾Ð´Ð½ÑÑ Ð¿Ð°ÑÐ°Ð¼ÐµÑÑÐ¾Ð²
     */
    Params: BaseParams;

    /**
     * ÐÐ¾Ð»Ð»ÐµÐºÑÐ¸Ñ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½ÑÐ¾Ð²
     */
    protected Collection: Array<T>;

    /**
     * Ð¡ÑÑÐ»ÐºÐ° Ð½Ð° Ð¿ÑÐ¸Ð»Ð¾Ð¶ÐµÐ½Ð¸Ðµ
     */
     public App: App;

    // #endregion

    constructor(params: BaseParams, app: App) {
        this.App = app;
        this.Params = params;
        this.Collection = new Array<T>();
    };

    // #region ÐÐµÑÐ¾Ð´Ñ

    /**
     * ÐÑÐ¿Ð¾Ð»Ð½ÑÐµÐ¼ Ð¿ÑÐ¸Ð²ÑÐ·ÐºÑ Ð¾Ð±ÑÐµÐºÑÐ° Ðº ÐºÐ¾Ð½ÐºÑÐµÑÐ½Ð¾Ð¼Ñ ÑÐ»ÐµÐ¼ÐµÐ½ÑÑ ÐÐÐ
     */
    private _bindCollection(elem: Element) {
        let that = this;
        // ÑÑÐ¾Ð±Ñ Ð¸Ð·Ð±ÐµÐ¶Ð°ÑÑ Ð¿Ð¾Ð²ÑÐ¾ÑÐ½Ð¾Ð³Ð¾ ÑÐ¾Ð·Ð´Ð°Ð½Ð¸Ñ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½ÑÐ° 
        //Log('_bindCollection:', $(elem).data('__instance'));
        if ($(elem).data('__instance') == undefined) {
            let item = that.Ð¡reate(elem, that.Params, that);
            $(elem).data('__instance', item);
            that.Collection.push(item);
        }
    }

    /**
     * ÐÑÐ¿Ð¾Ð»Ð½ÑÐµÐ¼ Ð¿ÑÐ¸Ð²ÑÐ·ÐºÑ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½ÑÐ¾Ð² Ð² ÐÐÐ
     */
    public BindCollection(): void {
        //Log('BindCollection:', this);
        let that = this;
        if (that.Params.Element) {
            that.Params.Element.forEach(function (elem: Element) {
                that._bindCollection(elem);    
            });
        }
        if (that.Params.Selector) {
            $(that.Params.Selector).each(function (index: Number, elem: Element) {
                that._bindCollection(elem);
            });
        }
    }

    /**
     * ÐÑÐ¿Ð¾Ð»Ð½ÑÐµÐ¼ Ð²ÑÐ·Ð¾Ð² Ð¼ÐµÑÐ¾Ð´Ð° action Ð½Ð° ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ°Ñ elements (Ð¸Ð»Ð¸ Ð½Ð° Ð²ÑÐµÑ ÐµÑÐ»Ð¸ elements=null) Ñ Ð¿Ð°ÑÐ°Ð¼ÐµÑÑÐ°Ð¼Ð¸ params
     */
    public Invoke(action: string, elements: Array<Element> = null, params: Array<any> | any = null): Array<any>
    {
        let that = this;
        // ÑÐ°Ðº ÐºÐ°Ðº Ð¼Ñ ÑÐ·Ð°ÐµÐ¼ apply Ð¿ÑÐ¸ Ð²ÑÐ·Ð¾Ð²Ðµ ÑÑÐ½ÐºÑÐ¸Ð¸ ÑÐ¾ Ð¿Ð°ÑÐ°Ð¼ÐµÑÑÑ Ð½Ð°Ð´Ð¾ Ð·Ð°Ð³Ð½Ð°ÑÑ Ð² Ð¼Ð°ÑÑÐ¸Ð²
        if (!(params instanceof Array)) {
            params = [params];
        }
        let res: Array<any> = [];
        for (let i: number = 0, l: number = that.Collection.length; i < l; i++) {
            let _e = that.Collection[i];
            let e: any = _e;
            if (elements != null && elements.indexOf(_e.Element) < 0) continue;
            if (typeof e[action] === 'function') {
                // ÑÑÐ¾Ð±Ñ Ð²ÑÐ·Ð²Ð°ÑÑ, Ð½Ð°Ð¿ÑÐ¸Ð¼ÐµÑ, Ð¼ÐµÑÐ¾Ð´ Ñ 2 Ð°ÑÐ³ÑÐ¼ÐµÐ½ÑÐ°Ð¼Ð¸ 
                // collection.Invoke("method", [param1, param2]);
                // Ð²ÑÐ·Ð¾Ð²ÐµÑ Ð½Ð° ÐºÐ°Ð¶Ð´Ð¾Ð¼ ÑÐ»ÐµÐ¼ÐµÐ½ÑÐµ
                // element.method(param1, param2);
                res.push(e[action].apply(e, params));
            } else {
                // ÐµÑÐ»Ð¸ Ð¾Ð±ÑÐµÐºÑ Ð² ÐºÐ¾Ð»Ð»ÐµÐºÑÐ¸Ð¸ Ð½ÐµÑÐ¾Ð´ÐµÑÐ¶Ð¸Ñ ÑÑÐ½ÐºÑÐ¸Ñ Ð·Ð½Ð°ÑÐ¸Ñ Ð¸ Ð²ÑÐµ Ð¾ÑÑÐ°Ð»ÑÐ½ÑÐµ Ð¿Ð¾ Ð¸Ð´ÐµÐµ ÑÐ¾Ð¶Ðµ ÐµÐµ Ð½Ðµ ÑÐ¾Ð´ÐµÑÐ¶Ð°Ñ (ÐºÐ¾Ð»Ð»ÐµÐºÑÐ¸Ñ ÑÐ¾ÑÑÐ¾Ð¸Ñ Ð¸Ð· Ð¾Ð´Ð¸Ð½Ð°ÐºÐ¾Ð²ÑÑ Ð¾Ð±ÑÐµÐºÑÐ¾Ð²)
                // ÑÐ´ÐµÐ»Ð°ÐµÐ¼ Ð²ÑÐ²Ð¾Ð´ ÑÐ¾Ð¾Ð±ÑÐµÐ½Ð¸Ñ Ð¾Ð± Ð¾ÑÐ¸Ð±ÐºÐµ (Ð¼Ð°Ð»Ð¾ Ð»Ð¸ Ð¾Ð¿ÐµÑÐ°ÑÐ°Ð»Ð¸ÑÑ)
                Log('Error: Action "' + action + '" in collection "' + (that.Params.Selector) + '" not founded.');
                break;
            }
        }
        return res;
    }

    /**
     * Ð£Ð´Ð°Ð»ÑÐµÐ¼ ÐµÐ´Ð¸Ð½Ð¸ÑÐ½ÑÐ¹ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½ÑÐ° 
     */
    public RemoveElement(element: Element): void {
        for (let i: number = 0, l: number = this.Collection.length; i < l; i++) {
            if (this.Collection[i].Element == element) {
                $(this.Collection[i].Element).data('__instance', undefined);
                this.Collection[i].Destroy();
                this.Collection.splice(i, 1);
                break;
            }
        }
    }

    /**
     * Ð¥ÐµÑÐ¸Ð¼ Ð²ÑÑ ÐºÐ¾Ð»Ð»ÐµÐºÑÐ¸Ñ ÑÐµÐ»Ð¸ÐºÐ¾Ð¼
     */
    public Destroy(): void {
        for (let i: number = 0, l: number = this.Collection.length; i < l; i++) {
            $(this.Collection[i].Element).data('__instance', undefined);
            this.Collection[i].Destroy();
        }
        this.Collection = null;
    }

    /**
     * Ð ÑÑÐ¾Ð¼ Ð¼ÐµÑÐ¾Ð´Ðµ Ð½ÐµÐ¾Ð±ÑÐ¾Ð´Ð¸Ð¼Ð¾ ÑÐ¾Ð·Ð´Ð°ÑÑ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½ÑÐ°.
     * Ð ÑÑÐ¾Ð¼ Ð¼ÐµÑÐ¾Ð´Ðµ Ð¿ÑÐ¾ÑÑÐ¾ Ð²ÑÐ·ÑÐ²Ð°ÐµÑÑÑ ÐºÐ¾Ð½ÑÑÑÑÐºÑÐ¾Ñ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑÐ° ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½ÑÐ°
     */
    public abstract Ð¡reate(element: Element, params: BaseParams, collection: BaseItemCollection<T>): T;

    // #endregion
}

